*{
    margin: 0px;
    padding: 0px;
    outline: none;
}
nav{
    width: 100%;
    height: 10vh;
    background-color: black;
    color: white;
    font-size: 15px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: sticky;
    top:0px;
    z-index: 1000
    
    ;
}
nav a{
    text-decoration: none;

}
nav a:hover{
    color: crimson;
}
.foot{
    line-height: 30px;
    width: 100%;
    background-color: black;
    color: white;
    text-align: center;
    font-size: 14px;

}
.crcon{
    width: 100%;
    height: 90vh;
    position: relative;
}
.crcon img{
    width: 100%;
    height: 100%;
}
.lyr{
    position: absolute;
    top:0px;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(100,200,190,0.7),rgba(72, 45, 141, 0.7),rgba(209, 44, 12, 0.7));
}
.dots{
    width: 100%;
    height: 20px;
    text-align: center;
    position: absolute;
    bottom: 10px;
    font-size: 18px;
    letter-spacing: 5px;
    color: darkblue;
}
.acrhead{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 0px 10px;
    box-sizing: border-box;
    background-color: burlywood;
}